<template><div class="markdown-body"><h1>时间选择器</h1>
<p><code v-pre="">mu-time-picker</code> 用于时间选择</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex&gt;\n    &lt;mu-checkbox class=\&quot;demo-picker-form\&quot; v-model=\&quot;landscape\&quot; label=\&quot;landscape\&quot;&gt;&lt;/mu-checkbox&gt;\n    &lt;mu-radio class=\&quot;demo-picker-form\&quot; v-model=\&quot;type\&quot; value=\&quot;ampm\&quot; label=\&quot;ampm\&quot;&gt;&lt;/mu-radio&gt;\n    &lt;mu-radio class=\&quot;demo-picker-form\&quot; v-model=\&quot;type\&quot; value=\&quot;24hr\&quot; label=\&quot;24hr\&quot;&gt;&lt;/mu-radio&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex justify-content=\&quot;between\&quot; align-items=\&quot;end\&quot; wrap=\&quot;wrap\&quot;&gt;\n    &lt;mu-paper :z-depth=\&quot;1\&quot;&gt;\n      &lt;mu-time-picker :time.sync=\&quot;time\&quot; :format=\&quot;type\&quot; :landscape=\&quot;landscape\&quot;&gt;&lt;/mu-time-picker&gt;\n    &lt;/mu-paper&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      time: undefined,\n      landscape: false,\n      type: 'ampm'\n    };\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-picker-form {\n  margin: 0 16px 16px;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex>
    <mu-checkbox class="demo-picker-form" v-model="landscape" label="landscape"></mu-checkbox>
    <mu-radio class="demo-picker-form" v-model="type" value="ampm" label="ampm"></mu-radio>
    <mu-radio class="demo-picker-form" v-model="type" value="24hr" label="24hr"></mu-radio>
  </mu-flex>
  <mu-flex justify-content="between" align-items="end" wrap="wrap">
    <mu-paper :z-depth="1">
      <mu-time-picker :time.sync="time" :format="type" :landscape="landscape"></mu-time-picker>
    </mu-paper>
  </mu-flex>
</mu-container>


</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-picker-form"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"landscape"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"landscape"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-picker-form"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"type"</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"ampm"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"ampm"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-picker-form"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"type"</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"24hr"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"24hr"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"between"</span> <span class="hljs-attr">align-items</span>=<span class="hljs-string">"end"</span> <span class="hljs-attr">wrap</span>=<span class="hljs-string">"wrap"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-paper</span> <span class="hljs-attr">:z-depth</span>=<span class="hljs-string">"1"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-time-picker</span> <span class="hljs-attr">:time.sync</span>=<span class="hljs-string">"time"</span> <span class="hljs-attr">:format</span>=<span class="hljs-string">"type"</span> <span class="hljs-attr">:landscape</span>=<span class="hljs-string">"landscape"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-time-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-paper</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">time</span>: <span class="hljs-literal">undefined</span>,
      <span class="hljs-attr">landscape</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">type</span>: <span class="hljs-string">'ampm'</span>
    };
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-picker-form</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">0</span> <span class="hljs-number">16px</span> <span class="hljs-number">16px</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="time-picker"><a class="header-anchor" href="#time-picker" aria-hidden="true">¶</a> Time Picker</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>时间选择器颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>time</td>
<td>选择的时间，支持 <code v-pre="">.sync</code> 修饰符</td>
<td>Date</td>
<td>—</td>
<td>new Date()</td>
</tr>
<tr>
<td>format</td>
<td>日期模式</td>
<td>String</td>
<td>ampm/24hr</td>
<td>ampm</td>
</tr>
<tr>
<td>no-display</td>
<td>是否不存在时间显示部分</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>landscape</td>
<td>是否横屏显示</td>
<td>Function</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="date-picker-events"><a class="header-anchor" href="#date-picker-events" aria-hidden="true">¶</a> Date Picker Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当选择日期改变时触发事件</td>
<td>(time, mode, finished)</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
.demo-picker-form {
  margin: 0 16px 16px;
}
</style>
<script>
export default {
  data () {
    return {
      time: undefined,
      landscape: false,
      type: 'ampm'
    };
  }
}
</script>


